import React from "react";
import { Button } from "antd";

//
let localVideo;

//
let localStream;
//PeerA
let peerConnA;

/**
 * 
 */
class PeerConnection extends React.Component {

  componentDidMount() {
    //
    localVideo = this.refs['localVideo'];


    //
    localVideo.addEventListener('loadedmetadata', () => {
      console.log(`: videoWidth: ${localVideo.videoWidth}px,  videoHeight: ${localVideo.videoHeight}px`);
    });





  }

  //
  start = async () => {
    console.log('');
    try {
      //
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true, video: true });
      console.log('');
      //
      localVideo.srcObject = stream;
      localStream = stream;
    } catch (e) {
      console.log("getUserMedia:" + e);
    }
  }

  //
  call = async () => {
    console.log('...');
    //
    const videoTracks = localStream.getVideoTracks();
    //
    const audioTracks = localStream.getAudioTracks();
    //
    if (videoTracks.length > 0) {
      //
      console.log(`: ${videoTracks[0].label}`);
    }
    //
    if (audioTracks.length > 0) {
      //
      console.log(`: ${audioTracks[0].label}`);
    }

    //ICE Server,Google
    let configuration = { "iceServers": [{ "url": "stun:stun.l.google.com:19302" }] };

    //RTCPeerConnection
    peerConnA = new RTCPeerConnection(configuration);
    console.log('PeerConnection:peerConnA');
    //Candidate
    peerConnA.addEventListener('icecandidate', this.onIceCandidateA);

    //ICE
    peerConnA.addEventListener('iceconnectionstatechange', this.onIceStateChangeA);





    //
    localStream.getTracks().forEach((track) => {
      //
      peerConnA.addTrack(track, localStream);
    });
    console.log('peerConnA');

    try {
      console.log('peerConnAOffer');
      //Offer
      const offer = await peerConnA.createOffer();
      //Offer
      await this.onCreateOfferSuccess(offer);
    } catch (e) {
      //Offer
      this.onCreateSessionDescriptionError(e);
    }
  }

  //
  onCreateSessionDescriptionError = (error) => {
    console.log(`SD: ${error.toString()}`);
  }

  //Offer
  onCreateOfferSuccess = async (desc) => {
    //peerConnAOfferSDP
    //console.log(`peerConnAOfferSDP\n${desc.sdp}`);
    console.log(`peerConnAOfferSDP`);
    console.log('peerConnAstart');
    try {
      //peerConnA
      await peerConnA.setLocalDescription(desc);
      this.onSetLocalSuccess(peerConnA);
    } catch (e) {
      this.onSetSessionDescriptionError();
    }

  }

  //
  onSetLocalSuccess = (pc) => {
    console.log(`${this.getName(pc)}:setLocalDescription`);
  }

  //
  onSetRemoteSuccess = (pc) => {
    console.log(`${this.getName(pc)}:setRemoteDescription`);
  }

  //SD
  onSetSessionDescriptionError = (error) => {
    console.log(`SD: ${error.toString()}`);
  }

  getName = (pc) => {
    return (pc === peerConnA) ? 'peerConnA' : 'peerConnB';
  }

  //Candidate
  onIceCandidateA = async (event) => {

    try {
      if(event.candidate){
        //peerConnACandidatepeerConnB
        //await peerConnB.addIceCandidate(event.candidate);
        //this.onAddIceCandidateSuccess(peerConnB);
      }
    } catch (e) {
      //this.onAddIceCandidateError(peerConnB, e);
    }
    console.log("***********************")
    console.log(`IceCandidate:\n${event.candidate ? event.candidate.candidate: '(null)'}`);
    console.log("#######################")
  }



  //Candidate
  onAddIceCandidateSuccess = (pc) => {
    console.log(`${this.getName(pc)}IceCandidate`);
  } 

  //Candidate
  onAddIceCandidateError = (pc, error) => {
    console.log(`${this.getName(pc)}IceCandidate: ${error.toString()}`);
  }

  //ICE
  onIceStateChangeA = (event) => {
    console.log(`peerConnAICE: ${peerConnA.iceConnectionState}`);
    console.log('ICE: ', event);
  }



  //
  hangup = () => {
    console.log('');
    //peerConnA
    peerConnA.close();

    //peerConnA
    peerConnA = null;

  }

  render() {
    return (
      <div className="container">
        <h1>
          <span>RTCPeerConnectionA</span>
        </h1>
        {/*  */}
        <video ref="localVideo" playsInline autoPlay muted></video>
        <div>
        <Button ref="startButton" onClick={this.start} style={{marginRight:"10px"}}></Button>
        <Button ref="callButton" onClick={this.call} style={{marginRight:"10px"}}></Button>
        <Button ref="hangupButton" onClick={this.hangup} style={{marginRight:"10px"}}></Button>
        </div>
      </div>
    );
  }
}
//
export default PeerConnection;
